import {
  View,
  Text,
  Image,
  RefreshControl,
  ScrollView,
  StyleSheet,
  Dimensions,
  FlatList,
  TouchableHighlight,
  TouchableOpacity,
  Alert,
} from "react-native";
import SafeAreaView from "react-native-safe-area-view";
import MaterialCommunityIcons from "react-native-vector-icons/MaterialCommunityIcons";
import * as Location from "expo-location";
import React, { useState, useCallback, useEffect } from "react";
import DropDownPicker from "react-native-dropdown-picker";

import * as Color from "../styles/Color";
import { Colors } from "react-native/Libraries/NewAppScreen";
import { getDistance } from "geolib";

const Feed = ({ user }) => {
  const help = [1, 2, 3, 4];
  const [categoryOpen, setCategoryOpen] = useState(false);
  const [categoryValue, setCategoryValue] = useState([]);
  const [categoryItems, setCategoryItems] = useState([
    { label: "General", value: "general" },
    { label: "Athletics", value: "athletics" },
    { label: "Children", value: "children" },
    { label: "Clothing", value: "clothing" },
    { label: "Entertainment", value: "entertainment" },
    { label: "Home", value: "home" },
    { label: "Kitchen", value: "kitchen" },
    { label: "Pet", value: "pet" },
    { label: "Technology", value: "technology" },
  ]);
  const [radiusOpen, setRadiusOpen] = useState(false);
  const [radiusValue, setRadiusValue] = useState([]);
  const [radiusItems, setRadiusItems] = useState([
    { label: "5km", value: "5" },
    { label: "15km", value: "15" },
    { label: "unlimited", value: "0" },
  ]);
  const [feed, setFeed] = useState([]);
  const [feedDisplay, setFeedDisplay] = useState([]);
  const [refreshing, setRefreshing] = React.useState(false);
  const [location, setLocation] = useState({});
  const [errorMsg, setErrorMsg] = useState(null);

  const wait = (timeout) => {
    return new Promise((resolve) => setTimeout(resolve, timeout));
  };

  const fetchData = async () => {
    try {
      const res = await fetch(
        `https://yl2dnogf69.execute-api.us-east-1.amazonaws.com/requests`,
        {
          method: "GET",
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json",
          },
        }
      );
      const resJson = await res.json();
      if (res.status === 200) {
        let result = [];
        let json_data = resJson.body;
        setFeed(json_data);
        setFeedDisplay(feed);
      } else {
        alert(res.status);
      }
    } catch (err) {
      alert(err);
    }
  };

  const onRefresh = React.useCallback(() => {
    setRefreshing(true);
    wait(2000).then(() => {
      fetchData();
      setRefreshing(false);
    });
  }, []);

  // const currentUserId = "4c6cab21-29cf-4977-a3e8-2beb008c3441";

  const acceptById = async (requestId) => {
    alert(requestId);
    alert(user);
    try {
      const res = await fetch(
        `https://yl2dnogf69.execute-api.us-east-1.amazonaws.com/requests/${requestId}`,
        {
          method: "PUT",
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json",
          },
          body: JSON.stringify({
            acceptedUserId: user,
          }),
        }
      );
      const resJson = await res.json();
      if (res.status === 200) {
        const cleanedRes = JSON.stringify(resJson);
        // const stringified = JSON.stringify(Array(cleanedRes)[0]);
        alert(cleanedRes);
        return cleanedRes;
      } else {
        // setResult("empty");
        alert(res.status);
      }
    } catch (err) {
      // setResult("error");
      alert(err);
    }
  };

  const createConfirmationAlert = (requestId) =>
    Alert.alert(
      "Accept",
      "Please confirm that you would like to accept this request",
      [
        {
          text: "Cancel",
          onPress: () => console.log("Cancel Pressed"),
          style: "cancel",
        },
        { text: "OK", onPress: () => acceptById(requestId) },
      ]
    );

  const onCategoryOpen = useCallback(() => {
    setRadiusOpen(false);
  }, []);

  const onRadiusOpen = useCallback(() => {
    setCategoryOpen(false);
  }, []);

  DropDownPicker.setMode("BADGE");

  useEffect(() => {
    fetchData();
  }, []);

  useEffect(() => {
    (async () => {
      let { status } = await Location.requestForegroundPermissionsAsync();
      if (status !== "granted") {
        setErrorMsg("Permission to access location was denied");
        return;
      }

      let location = await Location.getCurrentPositionAsync({});
      setLocation(location);
    })();
  }, []);

  let text = "Waiting..";
  if (errorMsg) {
    text = errorMsg;
  } else if (location) {
    text = JSON.stringify(location);
  }

  return (
    <SafeAreaView style={styles.container}>
      {/* <ScrollView
        contentContainerStyle={styles.scrollContainer}
        refreshControl={
          <RefreshControl refreshing={refreshing} onRefresh={onRefresh} />
        }
      > */}
      <View style={styles.filersContainer}>
        <DropDownPicker
          multiple={true}
          open={categoryOpen}
          value={categoryValue}
          items={categoryItems}
          setOpen={setCategoryOpen}
          setValue={setCategoryValue}
          setItems={setCategoryItems}
          showBadgeDot={false}
          style={{
            display: "flex",
            flexDirection: "row",
            margin: 5,
            backgroundColor: Color.GREEN3,
            borderColor: Color.GREEN3,
            width: Dimensions.get("window").width / 3,
            // zIndex: -1,
          }}
          onChangeValue={(value) => {
            console.log(categoryValue);
            // console.log(categoryValue);
            // for (const v of value) {
            //   console.log(v);
            // }
            if (categoryValue.length) {
              setFeedDisplay(
                feed.filter((item) => categoryValue.includes(item.category))
              );
            } else {
              setFeedDisplay(feed);
            }

            // console.log(feed.filter((i) => i.category === value));
          }}
          onOpen={onCategoryOpen}
          placeholder="Category"
        />
        <DropDownPicker
          multiple={false}
          open={radiusOpen}
          value={radiusValue}
          items={radiusItems}
          setOpen={setRadiusOpen}
          setValue={setRadiusValue}
          setItems={setRadiusItems}
          showBadgeDot={false}
          style={{
            display: "flex",
            flexDirection: "row",
            margin: 5,
            backgroundColor: Color.GREEN3,
            borderColor: Color.GREEN3,
            width: Dimensions.get("window").width / 3,
          }}
          onChangeValue={(value) => {
            alert(value);
            console.log(radiusValue);
            const filteredFeed = feed;
            setFeedDisplay(
              feed.filter((item) => item.category === "ELECTRONICS")
            );
          }}
          onOpen={onRadiusOpen}
          placeholder="Radius"
        />
      </View>
      {/* {feed.map((feedItem, index) => (
        <Text data={feed}>
          {index} : {feedItem.userId} NEEDS{" "}
          {feedItem.description || "no description"} ACCEPTED BY{" "}
          {feedItem.acceptedUserId || "no one"}
        </Text>
      ))} */}
      {/* <Text>CURRENT USER: {user}</Text> */}
      <FlatList
        refreshControl={
          <RefreshControl refreshing={refreshing} onRefresh={onRefresh} />
        }
        data={feedDisplay}
        renderItem={({ item, index }) => (
          <View style={styles.card}>
            <View style={styles.header}>
              <Text style={styles.request}>{item.request} </Text>
              <Text style={styles.distance}>
                {getDistance(
                  {
                    latitude: location.coords ? location.coords.latitude : 0,
                    longitude: location.coords ? location.coords.longitude : 0,
                  },
                  {
                    latitude: item.location
                      ? parseInt(item.location.split(", ")[0])
                      : 0,
                    longitude: item.location
                      ? parseInt(item.location.split(", ")[1])
                      : 0,
                  },
                  100
                ) / 1000}
                km
              </Text>
            </View>
            <Text style={styles.description}>{item.description} </Text>
            <View style={styles.imgContainer}>
              {index === 1 ? (
                <Image
                  style={styles.img}
                  source={{
                    uri: "data:image/jpeg;base64,",
                  }}
                />
              ) : (
                <></>
              )}
            </View>

            {/* <Text>
              {index}: {item.userId}{" "}
              {item.give == "true" ? "IS GIVING HELP FOR" : "NEEDS"}{" "}
              {item.description || "no description"} ACCEPTED BY{" "}
              {item.acceptedUserId || "no one"}
            </Text> */}
            <View style={styles.buttons}>
              {item.completed === false ? (
                <TouchableOpacity
                  style={styles.chat}
                  title="accept"
                  onPress={() => createConfirmationAlert(item.requestId)}
                >
                  <MaterialCommunityIcons
                    name="text-box-check-outline"
                    color={Color.GREEN4}
                    size={30}
                  />
                </TouchableOpacity>
              ) : (
                <></>
              )}
              <TouchableOpacity
                style={styles.chat}
                title="chat"
                onPress={() => console.log(item.requestId)}
              >
                <MaterialCommunityIcons
                  name="forum-outline"
                  color={Color.GREEN4}
                  size={30}
                />
              </TouchableOpacity>
            </View>
          </View>
        )}
      />
      {/* </ScrollView> */}

      {/* expanded: location, photos3Url, userId */}
    </SafeAreaView>
  );
};
export default Feed;

const styles = StyleSheet.create({
  container: {
    display: "flex",
    alignItems: "flex-end",
  },
  scrollContainer: {},
  filersContainer: {
    alignItems: "flex-end",
    display: "flex",
    flexDirection: "row",
    justifyContent: "space-around",
    marginLeft: 90,
    zIndex: 2000,
  },
  card: {
    margin: 10,
    backgroundColor: Color.GREEN2,
    padding: 10,
    width: Dimensions.get("window").width - 20,
    borderRadius: 5,
  },
  request: {
    alignContent: "flex-start",
    fontWeight: "500",
  },
  header: {
    flexDirection: "row",
    justifyContent: "space-between",
  },
  distance: {
    alignContent: "flex-end",
    fontWeight: "400",
  },
  description: {
    alignContent: "flex-start",
    fontWeight: "300",
    marginTop: 5,
  },
  imgContainer: {
    alignItems: "center",
  },
  img: {
    alignContent: "flex-end",
    marginTop: 5,
    width: 70,
    height: 70,
  },
  chat: {
    margin: 5,
    height: 31,
  },
  buttons: {
    alignItems: "center",
    flexDirection: "row",
    justifyContent: "flex-end",
    padding: 2,
    width: Dimensions.get("window").width - 50,
    height: 31,
    borderRadius: 5,
  },
});
